html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex-grow: 1;
}

.footer {
  width: 100%;
  padding: 20px;
  background-color: #f8f8f8;
  text-align: center;
  margin-top: auto;
}

.job-listings-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 80px; 
  background-color: #ffffff;
  min-height: calc(100vh - 160px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start; /* Карточки начинаются сверху */
  align-items: center; /* Центрируем по горизонтали */
}

.loading {
  font-size: 1.2rem;
  color: #333333;
  text-align: center;
}

.no-jobs {
  font-size: 1.2rem;
  color: #333333;
  text-align: center;
}

.job-card {
  background: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  transition: box-shadow 0.2s ease;
  width: 600px; /* Фиксированная ширина для карточек */
  margin-left: auto;
  margin-right: auto; /* Центрируем карточки */
}

.job-card:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.job-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #000000;
  margin-bottom: 5px;
}

.job-salary {
  font-size: 1rem;
  color: #333333;
  margin-bottom: 5px;
}

.job-company {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: #555555;
  margin-bottom: 5px;
}

.company-name {
  margin-right: 5px;
}

.verified {
  color: #1a73e8;
  font-size: 0.8rem;
}

.job-location {
  font-size: 0.9rem;
  color: #666666;
  margin-bottom: 5px;
}

.job-deadline {
  font-size: 0.9rem;
  color: #666666;
  margin-bottom: 5px;
}

.deadline-label {
  font-weight: 500;
  color: #444444;
}

.job-published {
  font-size: 0.9rem;
  color: #666666;
  margin-bottom: 10px;
}

.published-label {
  font-weight: 500;
  color: #444444;
}

.job-description-label {
  font-size: 0.9rem;
  font-weight: 500;
  color: #444444;
  margin-bottom: 5px;
}

.job-description {
  font-size: 0.9rem;
  color: #555555;
  margin-bottom: 15px;
  overflow-wrap: break-word; /* Перенос длинных слов */
  word-break: break-word; /* Дополнительная поддержка для переносов */
}

.apply-btn {
  background: #1a73e8;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background 0.2s ease;
}

.apply-btn:hover {
  background: #1557b0;
}
